import { makeSource, defineDocumentType } from '@contentlayer/source-files';
import { type } from 'os';

const Blog = defineDocumentType(() => ({
  name: 'Blog',
  filePathPattern: '**/**/*.mdx',
  fields: {
    title: {
      type: 'string',
      required: true,
    },
    publishAt: {
      type: 'date',
      required: true,
    },
    updatedAt: {
      type: 'date',
      required: true,
    },
    description: {
      type: 'string',
      required: true,
    },
    image: {
      type: 'string',
      required: true,
    },
    isPublished: {
      type: 'boolean',
      default: true,
    },
    author: {
      type: 'string',
      required: true,
    },
    tags: {
      type: 'list',
      of: { type: 'string' },
    },
  },
  computedFields: {
    url_path: {
      type: 'string',
      resolve: (doc) => `/blog/${doc._raw.filePath}`,
    },
  },
}));

export default makeSource({
  contentPath: 'content',
  documentTypes: [Blog],
});
